.output {
  margin: 0 0 40px 40px;
  /* border: 2px solid blue; */
  display: flex;
  justify-content: center;

 }
  
  .output .performance-facts {
    margin: 40px 10px auto;
    width: auto;
    padding: 0 20px;
  }
  .performance-facts {
    border: 1px solid black;
    margin: 20px auto;
    width: 280px;
    padding: 0.5rem;
    color: #000;
    font-family: Roboto;
  }
  .performance-facts__header {
    border-bottom: 10px solid black;
    padding: 0 0 0.25rem 0;
    margin: 0 0 0.5rem 0;
  }
  .performance-facts__header p {
    margin: 0;
    font-size: 18px;
    display: none;
  }
  .performance-facts table {
    border-collapse: unset;
    border-spacing: 0;
  }
  
  .performance-facts__table,
  .performance-facts__table--small,
  .performance-facts__table--grid {
    width: 100%;
  }
  .performance-facts__table th,
  .performance-facts__table--small th,
  .performance-facts__table--grid th,
  .performance-facts__table td,
  .performance-facts__table--small td,
  .performance-facts__table--grid td {
    font-weight: normal;
    text-align: left;
    padding: 0.25rem 0;
    border-top: 1px solid black;
  }
  
  .performance-facts__table #lkcal-val-cal {
    font-size: 35px;
    border: 0 none !important;
  }
  b,
  strong {
    font-weight: 700;
  }
  .performance-facts__table td:last-child,
  .performance-facts__table--small td:last-child,
  .performance-facts__table--grid td:last-child {
    text-align: right;
  }
  
  .performance-facts__table .blank-cell,
  .performance-facts__table--small .blank-cell,
  .performance-facts__table--grid .blank-cell {
    width: 1rem;
    border-top: 0;
  }
  
  .demo-analysis td {
    background: none !important;
  }
  .performance-facts__table th,
  .performance-facts__table--small th,
  .performance-facts__table--grid th,
  .performance-facts__table td,
  .performance-facts__table--small td,
  .performance-facts__table--grid td {
    font-weight: normal;
    text-align: left;
    padding: 0.25rem 0;
    border-top: 1px solid black;
  }
  .input-error {
    display: flex;
    align-items: center;
  }
  .input-error p {
    color: red;
    padding: 10px;
    padding-top: 350px;
    margin: 10px;
    text-align: justify;
  }
  /* styling for responsive view */
  
  @media screen and (max-width: 700px) {
    .output {
      margin: 0;
      display: flex;
      justify-content: center;
      margin-bottom: 20px;
    }
    .output .performance-facts {
      margin: 0;
      width: 320px;
      padding: 10px;
      text-align: center;
    }
  }
  @media screen and (max-width: 500px) {
    .output {
      margin: 0;
      display: flex;
      justify-content: center;
      margin-bottom: 20px;
    }
    .output .performance-facts {
      margin: 0;
      padding: 10px;
      width: 240px;
      text-align: center;
    }
  }
  @media screen and (max-width: 350px) {
    .output {
      margin: 0;
      /* border: 2px solid blue; */
      display: flex;
      justify-content: center;
      margin-bottom: 10px;
    }
    .performance-facts__table #lkcal-val-cal {
      font-size: 30px;
      padding: 10px;
      border: 0 none !important;
    }
  }
  